@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/Robotocondensed.woff2") format("woff2"), url("../fonts/Robotocondensed.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/Robotocondensedbold.woff2") format("woff2"), url("../fonts/Robotocondensedbold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.page {
  font-family: "PT Mono", "Ubuntu Mono", monospace;
  font-size: 16px;
  line-height: 21.5px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  height: 100%; }

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  align-content: start;
  width: 100%; }

.page__leveler {
  margin: 0 18.23%; }
  @media (min-width: 1024px) and (max-width: 1600px) {
    .page__leveler {
      margin: 0 8%; } }
  @media (max-width: 1023px) {
    .page__leveler {
      margin: 0 6.51%; } }
  @media (max-width: 767px) {
    .page__leveler {
      margin: 0 7.81%; } }
  @media (max-width: 767px) {
    .page__leveler--mobile-off {
      display: contents; } }

.page__header {
  padding: 30px 0; }
  @media (max-width: 1023px) {
    .page__header {
      padding: 20px 0; } }
  @media (max-width: 767px) {
    .page__header {
      padding: 15px 0 20px; } }

.page__footer {
  padding: 55px 0; }
  @media (max-width: 1023px) {
    .page__footer {
      padding: 50px 0; } }
  @media (max-width: 767px) {
    .page__footer {
      padding: 35px 0; } }

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f7; }

.header__content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 60px; }
  @media (min-width: 1024px) and (max-width: 1440px) {
    .header__content-wrapper {
      padding-right: 0; } }
  @media (max-width: 1023px) {
    .header__content-wrapper {
      padding-right: 10px; } }
  @media (max-width: 767px) {
    .header__content-wrapper {
      padding-right: 0; } }

.header__logo {
  margin-right: 30px;
  align-self: start; }
  @media (min-width: 1024px) and (max-width: 1440px) {
    .header__logo {
      margin-right: 20px; } }

.header__logo img {
  min-width: 215px;
  min-height: 55px; }
  @media (max-width: 767px) {
    .header__logo img {
      width: 35px;
      height: 35px;
      min-width: auto;
      min-height: auto; } }

.header__call {
  margin-top: 5px;
  align-self: start; }
  @media (max-width: 1023px) {
    .header__call {
      display: none; } }

.contacts {
  justify-self: center; }

.contacts ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fill, 250px);
  max-width: 600px;
  row-gap: 20px;
  margin-bottom: 10px; }
  @media (min-width: 1024px) and (max-width: 1440px) {
    .contacts ul {
      grid-template-columns: max-content max-content;
      gap: 20px;
      min-width: auto; } }
  @media (max-width: 1023px) {
    .contacts ul {
      justify-content: end;
      min-width: 100px;
      max-width: 300px;
      grid-template-columns: repeat(auto-fit, 40px);
      margin-bottom: 15px;
      gap: 20px; } }
  @media (max-width: 767px) {
    .contacts ul {
      margin-bottom: 8px;
      max-width: 200px; } }

.contacts li {
  margin: 0;
  padding: 0; }

.contacts a {
  color: #f2f5f7;
  display: grid;
  grid-template-columns: min-content auto;
  align-items: center;
  gap: 19px;
  line-height: 17px;
  transition: 0.3s ease; }
  .contacts a:hover {
    color: #00b2ff; }

.contacts svg {
  color: #00b2ff; }
  @media (max-width: 1023px) {
    .contacts svg:hover {
      color: #f2f5f7; } }

.contacts p {
  margin: 0;
  padding: 0; }
  @media (max-width: 1023px) {
    .contacts p {
      display: none; } }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f2f5f7;
  border-radius: 3px;
  border: 1px solid #f2f5f7;
  min-height: 40px;
  padding: 10px 15px 10px;
  transition: 0.3s ease;
  user-select: none;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  line-height: 16px;
  min-width: 160px; }
  .button:hover {
    color: #00b2ff;
    border-color: #00b2ff; }

.social ul {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 40px);
  gap: 20px;
  justify-content: center; }

.social li {
  margin: 0;
  padding: 0;
  list-style: none; }

.social a {
  cursor: pointer; }

.social svg {
  color: #f2f5f7;
  transition: 0.3s ease; }
  .social svg:hover {
    color: #00b2ff; }

.footer {
  background-color: #041827; }

.footer__inner-wrapper {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .footer__inner-wrapper {
      justify-content: start;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: max-content 1fr; } }
  @media (max-width: 767px) {
    .footer__inner-wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: none; } }

.footer__logo-wrapper {
  display: grid;
  align-content: start;
  gap: 35px; }
  @media (max-width: 1023px) {
    .footer__logo-wrapper {
      grid-column: 1/-1;
      grid-row: 1/-2;
      grid-template-columns: 215px 1fr;
      align-items: center;
      gap: 0;
      margin-bottom: 35px; } }
  @media (max-width: 767px) {
    .footer__logo-wrapper {
      grid-template-columns: none;
      grid-template-rows: none;
      gap: 20px;
      margin-bottom: 25px;
      padding: 0 7.81%; } }

@media (max-width: 1023px) {
  .footer__social {
    justify-self: end; } }

@media (max-width: 767px) {
  .footer__social {
    justify-self: center; } }

@media (max-width: 767px) {
  .chapters {
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    padding: 20px 7.81%; } }

.chapters h2 {
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 20px; }

.chapters ul {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: auto auto;
  gap: 10px;
  min-width: 470px; }
  @media (min-width: 1024px) and (max-width: 1280px) {
    .chapters ul {
      grid-template-columns: none;
      min-width: auto; } }
  @media (max-width: 1023px) {
    .chapters ul {
      grid-template-columns: none;
      min-width: auto; } }

.chapters li {
  margin: 0;
  padding: 0;
  list-style: none; }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="company"] {
    order: 1; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="products"] {
    order: 3; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="production"] {
    order: 5; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="mounting"] {
    order: 7; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="accessories"] {
    order: 2; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="delivery"] {
    order: 4; } }

@media (min-width: 1280px) and (max-width: 1920px) {
  .chapters li[data-chapter="contacts"] {
    order: 6; } }

.chapters a {
  line-height: 22px;
  color: #ffffff;
  opacity: 0.7;
  transition: 0.3s ease; }
  .chapters a:hover {
    opacity: 1; }

@media (max-width: 767px) {
  .address {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    padding: 20px 7.81%; } }

.address h2 {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: 0.05em;
  font-weight: 400;
  font-size: 15px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 20px; }

.address ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column; }

.address li {
  margin: 0;
  padding: 0;
  list-style: none; }
  .address li:not(:last-child) {
    margin-bottom: 10px; }
  @media (max-width: 767px) {
    .address li:first-child {
      margin-bottom: 15px; } }

.address a {
  color: #ffffff;
  opacity: 0.7;
  transition: 0.3s ease;
  line-height: 25px; }
  .address a:hover {
    opacity: 1; }
  @media (max-width: 1023px) {
    .address a {
      line-height: 22px; } }
  @media (max-width: 767px) {
    .address a {
      line-height: 19px;
      font-size: 14px; } }

@media (max-width: 767px) {
  .accordion h2 {
    margin: 0;
    position: relative;
    display: grid;
    align-items: center;
    transition: 0.3s ease; }
    .accordion h2::after, .accordion h2::before {
      content: "";
      width: 15px;
      height: 1px;
      background-color: #f2f5f7;
      position: absolute;
      justify-self: end; } }

@media (max-width: 767px) {
  .accordion--nojs h2,
  .accordion--open h2 {
    padding-bottom: 15px; }
    .accordion--nojs h2::before, .accordion--nojs h2::after,
    .accordion--open h2::before,
    .accordion--open h2::after {
      margin-bottom: 15px; }
    .accordion--nojs h2::before,
    .accordion--open h2::before {
      transform: rotate(0deg); } }

@media (max-width: 767px) {
  .accordion--close h2::before {
    transform: rotate(90deg); } }

@media (max-width: 767px) {
  .accordion--nojs .accordion__wrapper,
  .accordion--open .accordion__wrapper {
    overflow: visible;
    max-height: none; } }

@media (max-width: 767px) {
  .accordion--close .accordion__wrapper {
    overflow: hidden;
    max-height: 0; } }

